body {
    margin: 0%;
    background-color: $main-bg-color;
    /* background-color: black; */
}

html {
    box-sizing: border-box;
}
*::before,
*::after {
    box-sizing: inherit;
}
h1,
h2,
h3,
h4,
p {
    margin-top: 0;
    margin-bottom: 0;
}

ul {
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 0;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

ul {
    list-style: none;
    padding: 0;
}

a {
    text-decoration: none;
}